.home {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 64px);

    .user-info {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 40px;
        padding: 40px;
        border-radius: 5px;
        box-shadow:
            0px 0px 1.9px rgba(0, 0, 0, 0.02),
            0px 0px 4.5px rgba(0, 0, 0, 0.028),
            0px 0px 8.4px rgba(0, 0, 0, 0.035),
            0px 0px 28px rgba(0, 0, 0, 0.05);


        .user-header {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 40px;

            .avatar {
                width: em(200px);
                height: em(200px);
                background-color: #F5F9FE;
                border-radius: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                object-fit: cover;

                img {
                    border-radius: 100%;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .description {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                gap: 8px;
            }
        }

        .user-stats {
            display: flex;
            width: 100%;
            align-items: center;
            justify-content: space-around;

            .stat {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
            }

            .link {
                background-color: #2196F3;
                padding: 8px;
                border-radius: 5px;
                color: white;
            }
        }
    }
}


@media screen and (max-width: 500px) {
    .home .user-info .user-header .avatar {
        width: em(120px);
        height: em(120px);
    }

    .home .user-info .user-header .description {
        h1 {
            font-size: 25px;
        }

        h2 {
            font-size: 20px;

        }
    }

    .home .user-info .user-stats {
        h3 {
            font-size: 16px;
        }

        .link {
            font-size: 14px;

        }
    }
}